import React, { Component } from 'react';


class SnapOn extends Component{

    constructor(){

        super();
    }

    render(){
        return (
                <div className="companyDetails">
                    <h1> SnapOn Business Solutions</h1>
                    <h2>Contractor</h2>
                    <h3><i>Oct 2013 - Jul 2015</i></h3>
                    <h4><b>Responsibilities:</b></h4>
                    <ul>
                    <li>Part of CAPPS(Common Application) team working on TDD(test driven development). </li>
                    <li>Supported of all common functionalities such as like Paging, fetching data from DB, Navigation Control.  </li>
                    <li>Designed and Developed new functionalities for Navigation module which includes Catalog, VIN, QuickList, Part,Chapter, Pages etc.Enhanced functionalities like paging, filters, VIN.  </li>
                    <li>Enhanced queries for all three databases: Oracle, SQL Server, postgres  </li>
                    <li>Worked on JIRA to open, close or update tickets.  </li>
                    <li>Worked on maven scripts(pom.xml) to build the common application module and release the same.  </li>
                    <li>Part of migration team for migrating front-end legacy code to Angular js  </li>
                    </ul>
                    <h4><b>Environment : </b></h4>
                    <h4>Core java, Tomcat, Eclipse, SQL Developer, Postgresql, SQL Server, JUnit, Maven, Node js, javascript, jQuery, JIRA, Accurev, Jenkins, AngularJS 1.2 </h4>

                </div>
        );

    }
}

export default SnapOn;


