import React, { Component } from 'react';


class Teradata extends Component{

    constructor(){

        super();
    }

    render(){
        return (
                <div className="companyDetails">
                    <h1> Teradata </h1>
                    <h2><i>Full time - USA</i></h2>
                    <h3><i>Jul 2019 - At Present</i></h3>
                    <h4><b>Responsibilities:</b></h4>
                    <ul>
                    <li>Worked on Teradata applications like Appcenter, Unity, and Listener </li>
                    <li>Worked on Covalent bug fixes and create open source components. </li>
                    <li>Worked on Vantage UI and developed functionalities like file upload, guided tour. </li>
                    <li>Worked on graphs using echarts.js </li>
                    <li>Built open source (Covalent) components. </li>
                    <li>Worked on schematics, ng-add and ng-update </li>
                    <li>Worked on GraphQL to integrate the service layer. </li>
                    </ul>
                    <h4><b>Environment : </b></h4>
                    <h4>JavaScript, Typescript, Angular 8, Agile, TDD, Karma, Git, Jenkins, Docker, JIRA, GraphQL</h4>

                </div>
        );

    }
}

export default Teradata;


