import React, { Component } from 'react';


class Amex extends Component{

    constructor(){

        super();
    }

    render(){
        return (
                <div className="companyDetails">
                    <h1> American Express</h1>
                    <h2><i>Contractor - USA</i></h2>
                    <h3><i>Sept 2018 - Jul 2019</i></h3>
                    <h4><b>Responsibilities:</b></h4>
                    <ul>
                    <li>Developed rich and robust User Interface (UI) Components using HTML 5, CSS 3, ECMAScript 6, Reactjs, Redux (JavaScript Framework), Node js and Express.  </li>
                    <li>Build responsive user interface for all external and internal customers of American express. Examples include - file monitoring system, client setup, connectivity management.  </li>
                    <li>Perform unit and integration testing using Jest and Enzyme.  </li>
                    <li>Perform browser testing using docker, selenium and webdriver client io to confirm correct implementation of functionality on all supported browsers.  </li>
                    <li>Collaborated with other Software Developers while following Agile Development using RALLY (Peer Code Review, Daily Scrum Meetings, Requirement Gathering and Analysis, Issue Tracking etc.)  </li>
                    <li>Consume and expose REST (Representational State Transfer) api’s node and express js framework  </li>
                    <li>Designed and implemented the role-based security (authentication and authorization) for the application  </li>
                    <li>Redux framework to call REST (Representational State Transfer) apis and store data in global store.  </li>
                    <li>Onsite lead for offshore deliverables. </li>
                    </ul>
                    <h4><b>Environment : </b></h4>
                    <h4>JavaScript, React, Redux, Node, Express, Webpack, Babel, Agile, TDD, Jest, Git, Jenkins, Couch DB, Jest, Enzyme, Docker  </h4>

                </div>
        );

    }
}

export default Amex;


